/* Grid
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.container {
  position: relative;
  width: 100%;
  max-width: 1455px;
  margin: 0 auto;
  padding: 0 15px;
  box-sizing: border-box; }

.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px; }

.column,
.columns {
  width: 100%;
  float: left;
  box-sizing: border-box; }

