/* Typography
  –––––––––––––––––––––––––––––––––––––––––––––––––– */
h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 2rem;
  font-weight: 300; }

p {
  margin: 0; }

/* Forms
  –––––––––––––––––––––––––––––––––––––––––––––––––– */
input[type="email"],
input[type="number"],
input[type="search"],
input[type="text"],
input[type="tel"],
input[type="url"],
input[type="password"],
textarea,
select {
  height: 48px;
  padding: 6px 10px;
  /* The 6px vertically centers text on FF, ignored by Webkit */
  background-color: rgba(9, 19, 33, 0.0638304);
  color: #353535;
  border-radius: 4px;
  border: none;
  box-shadow: none;
  box-sizing: border-box; }

/* Removes awkward default styles on some inputs for iOS */
input[type="email"],
input[type="number"],
input[type="search"],
input[type="text"],
input[type="tel"],
input[type="url"],
input[type="password"],
input[type='submit'] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none; }

textarea {
  min-height: 65px;
  padding-top: 6px;
  padding-bottom: 6px; }

input[type="email"]:focus,
input[type="number"]:focus,
input[type="search"]:focus,
input[type="text"]:focus,
input[type="tel"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
textarea:focus,
select:focus {
  outline: 0; }

label,
legend {
  display: block;
  margin-bottom: .5rem;
  font-weight: 600; }

fieldset {
  padding: 0;
  border-width: 0; }

input[type="checkbox"],
input[type="radio"] {
  display: inline; }

label > .label-body {
  display: inline-block;
  margin-left: .5rem;
  font-weight: normal; }

/* Code
  –––––––––––––––––––––––––––––––––––––––––––––––––– */
code {
  padding: .2rem .5rem;
  margin: 0 .2rem;
  font-size: 90%;
  white-space: nowrap;
  background: #F1F1F1;
  border: 1px solid #E1E1E1;
  border-radius: 4px; }

pre > code {
  display: block;
  padding: 1rem 1.5rem;
  white-space: pre; }

/* Tables
  –––––––––––––––––––––––––––––––––––––––––––––––––– */
th,
td {
  padding: 12px 15px;
  text-align: left;
  border-bottom: 1px solid #E1E1E1; }

th:first-child,
td:first-child {
  padding-left: 0; }

th:last-child,
td:last-child {
  padding-right: 0; }

/* Spacing
  –––––––––––––––––––––––––––––––––––––––––––––––––– */
input,
textarea,
select,
fieldset {
  margin-bottom: 0; }

pre,
blockquote,
dl,
figure,
table,
form {
  margin-bottom: 1.5rem; }

/* Utilities
  –––––––––––––––––––––––––––––––––––––––––––––––––– */
.u-full-width {
  width: 100%;
  box-sizing: border-box; }

.u-max-full-width {
  max-width: 100%;
  box-sizing: border-box; }

.u-pull-right {
  float: right; }

.u-pull-left {
  float: left; }

/* Misc
  –––––––––––––––––––––––––––––––––––––––––––––––––– */
hr {
  margin-top: 3rem;
  margin-bottom: 3.5rem;
  border-width: 0;
  border-top: 1px solid #E1E1E1; }

/* Clearing
  –––––––––––––––––––––––––––––––––––––––––––––––––– */
/* Self Clearing Goodness */
.container:after,
.row:after,
.u-cf {
  content: "";
  display: table;
  clear: both; }

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: transparent;
  z-index: 100;
  transition: top .35s ease, padding .35s ease, background .35s ease; }
  .header.scrolled {
    position: fixed;
    top: -100px; }
  .header.fixed {
    top: 0;
    padding: 20px 0;
    background: rgba(7, 1, 37, 0.9);
    backdrop-filter: blur(5px); }
  .header-inner {
    width: 100%;
    padding: 35px 0;
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .header .logo {
    display: block;
    width: 132px; }
  .header ul {
    display: flex;
    align-items: center; }
    @media only screen and (max-width: 990px) {
      .header ul {
        position: fixed;
        top: 0;
        left: -101%;
        display: block;
        width: 100%;
        height: 100Vh;
        padding: 20px;
        background: #121623 url("../img/bg-eath.png") no-repeat top right;
        transition: left .35s;
        z-index: 1000; }
        .header ul.active {
          left: 0; } }
    .header ul li {
      padding-left: 70px; }
      @media only screen and (max-width: 990px) {
        .header ul li {
          padding-left: 0;
          padding-bottom: 50px;
          text-align: center; }
          .header ul li:first-child {
            padding-bottom: 100px; } }
      .header ul li:last-child {
        padding-left: 160px; }
        @media only screen and (max-width: 990px) {
          .header ul li:last-child {
            padding-left: 0; }
            .header ul li:last-child a {
              margin: auto; } }
      @media only screen and (max-width: 990px) {
        .header ul li a {
          font-size: 25px;
          line-height: 25px; } }

.mobile-menu {
  display: none; }
  @media only screen and (max-width: 990px) {
    .mobile-menu {
      display: flex;
      justify-content: space-between;
      align-items: center; }
      .mobile-menu .burger span {
        background: transparent; }
        .mobile-menu .burger span:before, .mobile-menu .burger span:after {
          transform: rotate(0deg); }
        .mobile-menu .burger span:before {
          top: 0;
          transform: rotate(-45deg); }
        .mobile-menu .burger span:after {
          top: 0;
          transform: rotate(45deg); } }

.burger {
  display: none; }
  @media only screen and (max-width: 990px) {
    .burger {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 40px;
      height: 40px;
      padding: 0;
      margin: 0;
      border: 0;
      background: transparent; }
      .burger span {
        position: relative;
        display: block;
        width: 100%;
        height: 3px;
        background: #82C8F4; }
        .burger span:before, .burger span:after {
          content: "";
          position: absolute;
          left: 0;
          display: block;
          width: 100%;
          height: 100%;
          background: #82C8F4;
          transition: all .35s ease; }
        .burger span:before {
          top: -10px; }
        .burger span:after {
          top: 10px; } }

.hero {
  background: #121623 url("../img/bg-main.png") no-repeat center;
  background-size: cover;
  width: 100%;
  height: 100vh; }
